.coin-history-page .am-list-body {
  background-color: #13141C;
}
.coin-history-page .assetsMain {
  height: 60px;
  margin: 15px 0px 30px 0px;
  border-radius: 6px;
  background-color: #191A23;
  padding: 10px 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.coin-history-page .assetsMain div {
  font-size: 0.875rem;
  color: #fff;
}
.coin-history-page .assetsMain .assetsLeft .tokenSymol {
  color: #fff;
  padding-bottom: 0.3125rem;
}
.coin-history-page .assetsMain .assetsLeft .balance {
  color: #999;
  font-size: 0.875rem;
}
.coin-history-page .assetsMain .assetsRight {
  color: #fff;
  text-align: right;
}
.coin-history-page .assetsMain .assetsRight .usdt {
  color: #fff;
  font-size: 0.875rem;
  padding-bottom: 0.3125rem;
}
.coin-history-page .assetsMain .assetsRight .tokenPrice {
  font-size: 0.875rem;
}
.coin-history-page .assetsMain .assetsRight .IN {
  color: #00FFD8;
}
.coin-history-page .assetsMain .assetsRight .OUT {
  color: #FF4F68;
}
.coin-history-page .title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.9375rem;
}
.coin-history-page .title .left {
  font-size: 1.125rem;
  font-weight: 400;
  color: #ffffff;
}
.coin-history-page .title .right {
  width: 15.625rem;
  font-size: 0.875rem;
  font-weight: 400;
  color: #FF0044;
}
.coin-history-page .history-type {
  margin-top: 0.625rem;
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
}
.coin-history-page .history-type .am-tabs-default-bar-content,
.coin-history-page .history-type .am-tabs-default-bar-tab {
  background: #13141C !important;
  color: #fff;
  position: relative;
}
.coin-history-page .history-type .am-tabs-default-bar-content:before,
.coin-history-page .history-type .am-tabs-default-bar-tab:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 1px;
  border-top: 1px solid #666;
  color: #666;
  transform-origin: 0 0;
  transform: scaleY(0.5);
}
.coin-history-page .history-type .thermal-list {
  width: 100%;
  height: 8.75rem;
  margin-top: 0.9375rem;
  padding-top: 1.125rem;
  padding-bottom: 1.125rem;
  padding-left: 1rem;
  padding-right: 1rem;
  border: 2px solid #666666;
  background: linear-gradient(-30deg, #333333, #111111);
}
.coin-history-page .history-type .thermal-list .thermal-title {
  display: flex;
  justify-content: space-between;
  color: #fff;
}
.coin-history-page .history-type .thermal-list .thermal-title .status {
  padding-left: 0.625rem;
  padding-right: 0.625rem;
  margin-right: 0.3125rem;
  background: #16ba7e;
  border-radius: 4px;
}
.coin-history-page .history-type .thermal-list .hold-time .link {
  margin-left: 0.625rem;
  color: #00d2ff;
}
.coin-history-page .history-type .thermal-list .lock-position,
.coin-history-page .history-type .thermal-list .share-bonus,
.coin-history-page .history-type .thermal-list .hold-time {
  display: flex;
  justify-content: space-between;
  color: #fff;
  margin-top: 1rem;
}
html:not([data-scale]) .am-list-body:before {
  border: none!important;
  height: 0px!important;
}
html:not([data-scale]) .am-modal-input-container:before {
  border: none!important;
  height: 0px!important;
}
